const defaultLegendTitleStyle = {
  paddingLeft: '6px', 
  height: 'auto',
  color: '#515151',
  fontFamily: "'Open Sans', sans-serif",
  fontSize: '14px',
  fontWeight: '700',
  letterSpacing: '-0.3px',}

